import WxValidate from '../../utils/WxValidate.js';
import { LOGIN } from "../../utils/api.js" 
export default {
	data() {
		return {
			topItem: [{
				title: "平台信息"
			}, {
				title: "服务介绍"
			}, {
				title: "产品特点"
			}],
			mobileCode: "获取验证码",
			time: 60,
			mobileTip:"",
			smsTip: "",
			mobile: "",
			smsCode: ""
		}
	},
	onLoad() {
		this.initValidate();
	},
	methods: {
		// 规则校验
		initValidate(){
			// 规则
			const rules = {
				mobileName:{required: true},
				smsCodeName:{required:true}
			}
			// 提示
			const messages = {
				mobileName:{required: "请输入手机号！"},
				smsCodeName:{required:"请输入验证码"}
			}
			this.WxValidate = new WxValidate(rules,messages)
		},
		
		// 获取验证码
		getMobileCode() {
			this.time = 60
			this.timer()
		},
		timer() {
			if (this.time > 0) {
				this.time--;
				this.mobileCode = "还剩" + this.time + "s"
				setTimeout(this.timer, 1000);
			} else {
				this.time = 0;
				this.mobileCode = "获取验证码";
			}
		},
		// 登录
		handleLogin(e){
			console.log(e.detail.value)
			const params = e.detail.value
			console.log(params)
			if(!this.WxValidate.checkForm(params)){
					const error = this.WxValidate.errorList[0]
					// 可以自定义提示
					uni.showToast({
						title: `${error.msg}`,
						icon: 'none'
					});
				return false			
			} else{
				uni.request({
					url:LOGIN,
					 data:params,
					 method:"POST",
					 header:{'Accept':'application/prs.fresh.v1+json'},
					 success: (res) => {
					 	console.log(res)
					 }
				})
			}
		}
	}
}
